.friendRequests {
  background-color: white;
  width: 900px;
  height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 10%;
}

.closeIcon {
  width: 900px;
  display: flex;
  justify-content: flex-end;
}

.notification-btn {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #55abb1;
}

@media (max-width: 950px) {
  .notification-btn {
    border-radius: 50%;
    width: 70px;
    height: 70px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #55abb1;
    position: absolute;
    margin-left: 500px;
    margin-top: 100px;
  }
  .friends {
    margin-top: 30px;
  }
}

@media (max-width: 500px) {
    .friendRequests {
        background-color: white; 
        width: 100% ; 
        height: 80vh; 
        border-radius: 0%;
    }
    .closeIcon {
        width: 100%;
        display: flex;
        justify-content: flex-end
     }
     .req-title {
        font-size: 30px;
     }
     .divider {
         width: 300px;
     }
     .notification-btn {     
        border-radius:50%;
        width: 70px;
        height: 70px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: #55ABB1;
        position: absolute;
        margin-left: 420px;
        margin-top: 100px;
    }
}
