.fly-left {
  -webkit-animation: flyInFromLeft ease-in-out 750ms;
          animation: flyInFromLeft ease-in-out 750ms;
}

@-webkit-keyframes flyInFromLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes flyInFromLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

.fly-right {
  -webkit-animation: flyInFromRight ease-in-out 750ms;
          animation: flyInFromRight ease-in-out 750ms;
}

@-webkit-keyframes flyInFromRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(+50%);
            transform: translateX(+50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes flyInFromRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(+50%);
            transform: translateX(+50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

.fly-top {
  -webkit-animation: flyInFromTop ease-in-out 750ms;
          animation: flyInFromTop ease-in-out 750ms;
}

@-webkit-keyframes flyInFromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-80%);
            transform: translateY(-80%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes flyInFromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-80%);
            transform: translateY(-80%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

.fly-bottom {
  -webkit-animation: flyInFromBottom ease-in-out 750ms;
          animation: flyInFromBottom ease-in-out 750ms;
}

@-webkit-keyframes flyInFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(+50%);
            transform: translateY(+50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes flyInFromBottom {
  0% {
    opacity: 0;
